@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}a{text-decoration:none;cursor:pointer}

body{font-family:'Inter', sans-serif;color:#fff;background:#23252C;display:grid;place-content:center;height:100vh}.card{max-width:317px;border-radius:0.625em;background:#1A1B21}

.info{text-align:center}.info .content{padding:1em 2em}.info .info-avatar{height:317px;width:317px;border-radius:10px 10px 0px 0px;object-fit:cover;object-position:0 25%}.info .info-detail--name{font-weight:700;font-size:1.56em}.info .info-detail--job{font-size:0.8em;color:#F3BF99}.info .info-detail--website{font-size:0.64em}.info .info-buttons{display:flex;grid-gap:1em;gap:1em;margin:1em 0}.info .info-buttons--email{background:#fff;color:#374151;padding:1em 2em;font-size:0.875em;font-weight:500;border-radius:0.375em}.info .info-buttons--linkedin{background:#5093E2;color:#fff;padding:1em 2em;font-size:0.875em;font-weight:500;border-radius:0.375em}

.about .content{padding:0 2em .5em 2em}.about .about--title{color:#F5F5F5;font-weight:700;line-height:1.5em}.about .about--details{color:#DCDCDC;font-size:0.7em;line-height:150%}

.interest .content{padding:.5em 2em 2em 2em}.interest .interest--title{color:#F5F5F5;font-weight:700;line-height:1.5em}.interest .interest--details{color:#DCDCDC;font-size:0.7em;line-height:150%}

.footer .content{padding:1.5em;background:#161619;border-radius:0 0 0.625em 0.625em;display:flex;justify-content:center;grid-gap:1em;gap:1em}.footer .footer-button{color:#918E9B}

